
.country-list {
display: grid;
justify-content: center;
flex-wrap: wrap;
padding-inline: 0;
gap:40px;
grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
}

.country-list > li {
    display: flex;
    flex-direction: column;
    text-align: left;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.207);
    border-radius: 6px;
    overflow: hidden;
    cursor: pointer;
}


.item-info {
    padding: 5px 20px 5px 20px;
}

.item-info > p {
margin-block: 8px;
}