main {
   display: flex;
   flex-direction: column;
   padding: 100px 20px 20px 20px;
   max-width: 1300px;
   margin: 0 auto;
   min-height: 100vh;
}

.search {
    display: flex;
    justify-content: space-between;
    margin-block: 30px;
    height: 40px;
}

input[type=text] {
    width: clamp(130px,30vw,400px);
    border: none;
    padding-inline: 10px;
    background-color: transparent;
}

input[type=text]:focus {
outline: none;
}

input[type=text]:focus::placeholder {
    visibility: hidden;
}

.input-div {
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.207);
    height: 40px;
    display: flex;
    align-items: center;
    padding-left: 10px;
    border-radius: 6px;
}

.filter-container {
    position: relative;
    border-radius: 6px;
}

.filter-btn {
    border: none;
    background-color: transparent;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.207);
    cursor: pointer;
    
    height: 40px;
    width: clamp(130px, 37vw, 200px);
}

.filter-drop {
    list-style: none;
    position: absolute;
    padding: 5px 0;
    width: clamp(160px, 40vw, 200px);
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.207);
    border-radius: 6px;
    margin-top: 4px;
}

.filter-drop > li {
    padding: 6px 15px;
    cursor: pointer;
}

.filter-drop > li:hover {
background-color: rgba(128, 128, 128, 0.216);
}

.fa-arrow-down {
    padding-left: 10px;
}

.light-main {
    background-color: hsl(0, 0%, 98%)
}

.dark-main {
    background-color: hsl(207, 26%, 17%);
}