.back {
    padding-inline: 10px;
    width: 120px;
    height: 40px;
    border-radius: 6px;
    border: none;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.207);
    margin-block: 100px;
    cursor: pointer;
}

.back:hover {
    background-color: rgba(128, 128, 128, 0.216);
}

.fa-arrow-left {
    padding-right: 10px;
}

.item {
    display: flex;
   
}

.info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 20px;
    width: 50%;
}

.description {
display: flex;
justify-content: space-between;
gap: 20px;
}

.info-left > ul {
    display: flex;
    flex-direction: column;
   list-style: none;
   padding-left: 0;
   gap: 10px;
}

.info-left {
    width: 50%;
}


.info-right > ul {
    display: flex;
    flex-direction: column;
   list-style: none;
   padding-left: 0;
   gap: 10px;
}

.info-right {
    width: 50%;
}

.focus-image {
    width: 50%;

}

.country-container {
    display: flex;
    align-items: center;
    margin-top: 40px;
    flex-wrap: wrap;
    gap: 10px
}

.countries {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
}

.country {
    padding-inline: 10px;
    width: clamp(80px, 50vw, 100px);
    height: 30px;
    border-radius: 4px;
    border: none;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.207);
    display: flex;
    justify-content: center;
    align-items: center;
   
}



@media screen and (max-width: 1037px){
    .focus-image {
        width: 100%;
    }

.info {
    width: 100%;
}

    .info-left {
        width: 100%;
    }

    .info-right {
        width: 100%;
    }

    .item {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .description {
        align-items: center;
        flex-direction: column;
        width: 100%;
    }

    .country-container {
        flex-direction: column;
        align-items: flex-start;
        margin-top: 30px;
    }

    .countries {
        margin: 20px 0;
    }
}