header {
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.207);
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 2;
}

.menu {
    height: 100px;
    margin:auto;
    max-width: 1300px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-inline: 20px;
}

.mode-btn {
    display: flex;
    gap: 10px;
    align-items: center;
    cursor: pointer;
}